import React from "react";

import Navbar from "components/Navbars/Navbar.js";
import Footer from "components/Footers/Footer.js";

import IndexHero from "./IndexSections/IndexHero.js";
import IndexContactCards from "./IndexSections/IndexContactCards";
import IndexAbout from "./IndexSections/IndexAbout";
import IndexFeatures from "./IndexSections/IndexFeatures";

class Index extends React.Component {
  componentDidMount() {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    this.refs.main.scrollTop = 0;
  }
  render() {
    return (
      <>
        <Navbar />
        <main ref="main">
          <IndexHero />
          <IndexContactCards />
          <IndexAbout />
          <IndexFeatures />
        </main>
        <Footer />
      </>
    );
  }
}

export default Index;
